.aboutContainer {
  max-width: 1240px;
  margin: auto;
  margin-bottom: 50px;
}



.aboutBanner {
  width: 100%;
  height: 223px;
  margin-bottom: 35px;
  position: relative;

  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0.3;
    border-radius: 25px;
  }

  img.image {
    width: 100%;
    height: 223px;
    border-radius: 25px;
    object-fit: cover;
  }
}